





































































	import uniSwiperDot from '@/components/uni-swiper-dot/uni-swiper-dot'
	import mTitle from '@/components/m-title'
	import mHomePro from '@/components/m-home-pro'
	import mHomeHotpro from '@/components/m-home-hotpro'
	import mHomeStore from '@/components/m-home-store'
	export default {
		data() {
			return {
				isLogin:false,
				searchValue:"",
				afterHeaderOpacity: 1,//不透明度
				headerPosition: 'fixed',
				headerTop:null,
				statusTop:null,
				// 轮播图片
				swiperList: [],
				// 热卖
				hotsellPage:1,
				hotProList:[],
				// 拼团列表
				groupsellList:[],
				// 附近门店
				nearStoreList:[{
// 					img:"../../static/img/2.jpg",
// 					title:"老萌1号店",
// 					distance:"13km",
// 					describel:"优惠优惠优惠",
// 					address:"北京市海淀区中关村大街15号",
// 					tips:['优惠']
				}],
				current: 0,
				mode: 'long',
			};
		},
		components: {
			uniSwiperDot,
			mTitle,
			mHomePro,
			mHomeHotpro,
			mHomeStore
		},
		methods:{
			//是否登录了
			checkLogin(){
				let _this = this;
				_this.globelIsLogin().then(res=>{
					if(res=='success'){
						//已登录
						_this.isLogin=true;
						
					}
				}).catch(err=>{
					_this.isLogin=false
				});
			},
			//首页搜索
			toSearch(){
				this.linkTo("/pages/product/productlist?search="+this.searchValue)
			},
			// banner图片
			getBanners(){
				this.mGet('/server/b/banners',{}).then(res=>{
					if(res.code=1){
						this.swiperList=res.data;
					}
				}).catch(err=>{
					console.log(err);
				});
			},
			//热卖列表
			getHotsellList(){
				console.log('热卖');
				this.mPost('/server/p/hot/products',{
					start:this.hotsellPage,
					length:3
				}).then(res=>{
					console.log('这里');
					console.log(res);
					if(res.code=1){
						if(res.data){
							let data = res.data;
							this.hotProList = data.list;
							this.hotsellPage=data.nextPage;
						}
					}
				}).catch(err=>{
					console.log(err);
				});
			},
			//拼团列表
			getGroupsellList(){
				let _this=this;
				this.mPost('/server/p/group/products',{
					start:this.hotsellPage,
					length:500
				}).then(res=>{
					if(res.code=1){
						if(res.data){
							let data = res.data;
							if(data.list){
								_this.groupsellList = data.list;
							}
						}
					}
				}).catch(err=>{
					console.log(err);
				});
			},
			//门店列表
			getStoreList(lng,lat){
				let _this=this;
				_this.mPost('/server/s/vicinity/stores',{
// 					"lng":lng || 116.206845,
// 					"lat":lat || 39.762155,
					"lng": 116.206845,
					"lat": 39.762155
				}).then(res=>{
					if(res.code=1){
						if(res.data){
							let data = res.data;
								_this.nearStoreList = data;
						}
					}
				}).catch(err=>{
					console.log(err);
				});
			},
			// 门店更多
			choseStore(){
				this.linkTo("/pages/store/list")
			},
			//点击热卖图片
			hotProDetail(item){
				this.linkTo("/pages/store/store?storeid="+item.storeId)
			},
			// 点击拼团图片
			groupProDetail(){
				this.linkTo("/pages/product/product")
			},
			//点击门店图片
			storeDetail(){
				this.linkTo("/pages/store/store")
			},
			swiperChange(e) {
				this.current = e.detail.current;
			},
			// 拼团
			pintuanHandle(){
				this.linkTo("/pages/groupbuy/groupbuy")
			},
			//附近门店
			storeHandle(){
				this.linkTo("/pages/store/list")
				
			},
			linkTo(url){
				if(this.isLogin){
					uni.navigateTo({
						url:url
					})
				}else{
					uni.navigateTo({
						url:"/pages/login/login"
					})
				}
			}
		},
		onLoad(){
			this.checkLogin();
			let _this = this;
			uni.getLocation({//获取当前的位置坐标
				type: 'wgs84',
				success: function (res) {
					_this.getStoreList(res.longitude,res.latitude);
					console.log('当前位置的经度：' + res.longitude);
					console.log('当前位置的纬度：' + res.latitude);
				}
			}); 
			this.getBanners();
			this.getHotsellList();
			this.getGroupsellList();
		}
	}
